import classes from "./App.module.css";
import { useState, useEffect, useRef } from "react";
import { Player, Controls } from "@lottiefiles/react-lottie-player";
import Lottie from "lottie-web";
import { motion, useAnimation } from "framer-motion";
import pforeground from "./img/pforeground.png";
import pbackground from "./img/parallaxbackground.png";
import vp from "./img/vp.png";
import coyote from "./img/coyote.png";
import robinson from "./img/chrobinson.png";
import jbhunt from "./img/jbhunt.png";
import { createContext } from "react";
import Nav from "./components/Nav";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import Contact from "./components/Contact";
import Home from "./components/Home";
import Services from "./components/Services";

export const openContext = createContext(false);

function App() {
  const [offestX, setOffsetX] = useState(0);
  const handleScroll = () => setOffsetX(window.pageYOffset);

  const [ navBar ,setNavbar ] = useState(false);
  const changeNavBack = () => {
    if(window.scrollY >= 80){
      setNavbar(true);
    }
    else{
      setNavbar(false);
    }
  }

  window.addEventListener('scroll', changeNavBack);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const vpAnimation = useAnimation();
  const ref = useRef();

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          vpAnimation.start({
            x: "-30%",
            opacity: 1,
          });
          observer.unobserve(entry.target);
        }
      },
      {
        threshold: 0.1,
      }
    );
    if (ref.current) {
      observer.observe(ref.current);
    }
  }, [ref]);

  const [isOpen, setOpen] = useState(false);

  const burger = useRef(null);
  useEffect(() => {
    burger.current = Lottie.loadAnimation({
      container: burger.current,
      renderer: "svg",
      loop: false,
      autoplay: false,
      animationData: require("./img/burger.json"),
    });

    return () => burger.current?.destroy();
  }, []);

  return (
    <openContext.Provider
      value={{ value: [isOpen, setOpen], burgers: [burger] }}
    >
      <BrowserRouter>
        <Nav />
        <div className={classes.pagecontainer} >
          <div className={navBar ? classes.navActive : classes.nav }>
            <div className={classes.logo}>
              <Player
                autoplay
                keepLastFrame
                src="https://assets10.lottiefiles.com/packages/lf20_llsjflyx.json"
              >
                <Controls visible={false} />
              </Player>
            </div>

            <button
              className={classes.burger}
              onClick={() => {
                setOpen(!isOpen);
                burger.current?.setDirection(isOpen ? -1 : 1);
                burger.current?.play();
              }}
              ref={burger}
            />
            <div className={classes.navlinks}>
              <motion.li
                whileHover={{ textDecoration: "underline .25rem #df4426" }}
                className={classes.li}
              >
                <Link to="/" style={{color: '#414143' , textDecoration: 'none'}} >HOME</Link>
              </motion.li>
              <motion.li
                whileHover={{ textDecoration: "underline .25rem #df4426" }}
                className={classes.li}
              >
               <Link to="/services" style={{color: '#414143' , textDecoration: 'none'}} > SERVICES</Link>
              </motion.li>
              <motion.li
                whileHover={{ textDecoration: "underline .25rem #df4426" }}
                className={classes.li}
              >
                <Link to="/contactus" style={{color: '#414143' , textDecoration: 'none'}} > CONTACT US </Link>
              </motion.li>
            </div>
          </div>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/services" element={ <Services/> } />
            <Route path="/contactus" element={<Contact />} />
          </Routes>
          
        </div>
      </BrowserRouter>
    </openContext.Provider>
  );
}

export default App;
