import { motion } from "framer-motion";
import { AnimatePresence } from "framer-motion";
import { openContext } from "../App";
import { useContext } from'react';
import classes from './Nav.module.css'
import { Link } from 'react-router-dom'

function Nav(props) {
  const { value , burgers } = useContext(openContext);
  const [isOpen, setOpen] = value;
  const burger = burgers;
  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div
          animate={{ x: '0%', opacity: 1 }}
          initial={{ x: '-75%' , opacity: 0 }}
          exit={{ x: '-75%' , opacity: 0 }}
          className={classes.nav}
        >
          <ul className={classes.links} >
              <li className={classes.li} onClick={() => {setOpen(!isOpen); burger.current?.setDirection(isOpen ? -1 : 1); burger.current?.play();}} ><Link to="/">HOME</Link></li>
              <li className={classes.li} onClick={() => {setOpen(!isOpen); burger.current?.setDirection(isOpen ? -1 : 1); burger.current?.play();}} ><Link to="/services">SERVICES</Link></li>
              <li className={classes.li} onClick={() => {setOpen(!isOpen); burger.current?.setDirection(isOpen ? -1 : 1); burger.current?.play();}} ><Link to="/contactus">CONTACT US</Link></li>
          </ul>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
export default Nav;
